import { Expense } from "./ExpenseForm";

interface Props {
  expenses: Expense[];
}

export default function ExpenseList({ expenses }: Props) {
  return (
    <div style={{ padding: "1rem" }}>
      <h3>Expense List</h3>
      <ul>
        {expenses.map((e, index) => (
          <li key={index}>
            {e.title} - ₹{e.amount} | {e.category} | {e.date}
          </li>
        ))}
      </ul>
    </div>
  );
}
