// pages/index.tsx

import { useState } from "react";
import BudgetTracker from "components/BudgetTracker";
import Charts from "components/Charts";
import ExpenseForm from "components/ExpenseForm";
import ExpenseList from "components/ExpenseList";
import ExportButtons from "components/ExportButtons";
import { NextPage } from "next";

export type Expense = {
  id: string;
  title: string;
  amount: number;
  category: string;
  date: string;
};

const Home: NextPage = () => {
  const [expenses, setExpenses] = useState<Expense[]>([]);
  const [budget, setBudget] = useState<number>(5000); // or your default

  const handleAddExpense = (expense: Omit<Expense, "id">) => {
    const newExpense: Expense = {
      ...expense,
      id: Date.now().toString(), // simple unique ID
    };
    setExpenses((prev) => [...prev, newExpense]);
  };

  return (
    <main style={containerStyle}>
      <h1 style={headingStyle}>💸 Welcome to the Expense Tracker</h1>
      <p style={textStyle}>
        Track your spending, set budgets, and visualize your expenses easily.
      </p>

      <div style={{ marginTop: "2rem" }}>
        <BudgetTracker expenses={expenses} budget={budget} />
        <ExpenseForm onAdd={handleAddExpense} />
        <ExpenseList expenses={expenses} />
        <Charts data={expenses} />
        <ExportButtons data={expenses} />
      </div>
    </main>
  );
};

export default Home;

// ---------- Inline styles ----------
const containerStyle: React.CSSProperties = {
  padding: "2rem",
  fontFamily: "Arial, sans-serif",
  textAlign: "center",
};

const headingStyle: React.CSSProperties = {
  fontSize: "2rem",
  fontWeight: "bold",
};

const textStyle: React.CSSProperties = {
  fontSize: "1.1rem",
  color: "#555",
};
